.standardHeader {
  display: flex;
  place-content: center;
}
.standardHeader nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 85%;
}
.standardHeader nav .logoContainer {
  display: block;
  width: 80px;
}
.standardHeader nav .navLinks {
  display: flex;
}
.standardHeader nav .navLinks li {
  list-style: none;
  transition: color 0.3s;
  padding-left: 50px;
}
.standardHeader nav .navLinks li a {
  font-size: 1.125em;
  font-weight: 500;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
}
.standardHeader nav .navLinks li a:hover {
  color: #ff5722;
}
.standardHeader nav .navLinks li.active a {
  color: #ff5722;
}
.standardHeader nav button {
  all: unset;
  display: none;
}
.standardHeader nav .screenDarkener {
  display: block;
  position: fixed;
  visibility: collapse;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: black;
  opacity: 0;
  transition: visibility 0s, opacity 300ms;
}
@media screen and (max-width: 800px) {
  .standardHeader {
    background-color: black;
  }
  .standardHeader nav {
    width: 90%;
  }
  .standardHeader nav .logoContainer {
    width: 60px;
    margin: auto;
  }
  .standardHeader nav .sidenavTrigger {
    all: unset;
    position: absolute;
    cursor: pointer;
    color: white;
  }
  .standardHeader nav .sidenavTrigger i {
    font-size: 1.875em;
  }
  .standardHeader nav .navLinks {
    display: block;
    position: fixed;
    margin: 0;
    padding: 0;
    width: 300px;
    top: 0;
    bottom: 0;
    z-index: 10;
    left: -300px;
    background-color: #111111;
    transition: left 300ms;
  }
  .standardHeader nav .navLinks li {
    padding: 0;
    transition: background-color 0.3s;
  }
  .standardHeader nav .navLinks li a {
    display: block;
    box-sizing: border-box;
    padding: 20px;
    width: 300px;
  }
  .standardHeader nav .navLinks li.active {
    background-color: rgba(0, 0, 0, 0.2);
  }
  .standardHeader nav .navLinks li.active a {
    color: white;
  }
  .standardHeader nav .navLinks li:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .standardHeader nav .navLinks li:hover a {
    color: white;
  }
}

